import FontAwesome from "react-fontawesome";
export default function Footer() {
    return (
        <footer>
            <div className="container">
                <div className="box">
                    <div className="box-left">
                        <a href="" className="footer-logo">
                            <img src="images\systems\logo-yellow.png" alt="" className="logo" />
                        </a>
                        <p className="description">
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis ipsum suspendisse ultrices gravida.
                            Risus commodo viver ra maecenas accumsan lacus vel facilisis.
                        </p>
                    </div>
                    <div className="box-right box-contact">
                        <a href="" className="address">
                            <FontAwesome name="map-marker"/>
                            <span className="text">Main Road, Building Name, Country</span>
                        </a>
                        <a href="" className="email">
                            <FontAwesome name="envelope"/>
                            <span className="text">info@companyname.com</span>
                        </a>
                    </div>
                </div>
                <div className="copy-right">
                    <p className="title">© Company Name 2020. All rights reserved.</p>
                    <ul className="list-icon-contact">
                        <li className="contact-item"><a href="" className="contact-link"><FontAwesome name="instagram"/></a></li>
                        <li className="contact-item"><a href="" className="contact-link"><FontAwesome name="facebook"/></a></li>
                        <li className="contact-item"><a href="" className="contact-link"><FontAwesome name="twitter"/></a></li>
                        <li className="contact-item"><a href="" className="contact-link"><FontAwesome name="whatsapp"/></a></li>
                    </ul>
                </div>
            </div>
        </footer>
    );
}