export default function BoxBookingHome() {
    return (
        <section className="box-submit box-booking hero-box text-center">
            <p className="top-title">Reservation</p>
            <h2 className="title">Book your table</h2>
            <form action="" className="form-booking">
                <div className="form-group">
                    <input type="text" className="form-control" placeholder="Name"/>
                </div>
                <div className="form-group">
                    <input type="text" className="form-control" placeholder="Email"/>
                </div>
                <div className="form-group">
                    <input type="text" className="form-control" placeholder="Date"/>
                </div>
                <div className="form-group">
                    <input type="text" className="form-control" placeholder="Time"/>
                </div>
                <div className="form-group">
                    <input type="text" className="form-control" placeholder="People"/>
                </div>
                <div className="form-group">
                    <button className="btn btn-full">Find A Table</button>
                </div>
            </form>
        </section>
    )
}